
var mjSprite = []
cc.Class({
    extends: cc.Component,

    properties: {
        leftAtlas:cc.SpriteAtlas,//左边的plist图片
        rightAtlas:cc.SpriteAtlas,//右边的plist图片
        bottomAtlas:cc.SpriteAtlas,//对面的plist图片
        myAtlas:cc.SpriteAtlas,//我的plist图片
        emptyAtlas:cc.SpriteAtlas,//空
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        wsft.mjCard = this
        this._sides = ["myself","right","up","left"];
        this._pres = ["M_","R_","B_","L_"]
        //筒
        for(var i = 1;i<10;++i){
            mjSprite.push("B_"+i)
        }
        //条
        for(var i = 1;i<10;++i){
            mjSprite.push("T_"+i)
        }
        //万
        for(var i = 1;i<10;++i){
            mjSprite.push("W_"+i)
        }
        //中发白
        mjSprite.push("C_zhong")
        mjSprite.push("C_fa")
        mjSprite.push("C_bai")
        //东南西北
        mjSprite.push("C_dong")
        mjSprite.push("C_nan")
        mjSprite.push("C_xi")
        mjSprite.push("C_bei")

    },
    getMJSpriteByID:function(id){
        return mjSprite[id];
    },
    getMJType:function(id){
        if(id >= 0 && id < 9){
            return 0;
        }
        else if(id >= 9 && id < 18){
            return 1;
        }
        else if(id >= 18 && id < 27){
            return 2;
        }
    },
    getSpriteFrameByMJID:function(pre,mjid){
        var spriteFrameName = this.getMJSpriteByID(mjid);
        spriteFrameName = pre + spriteFrameName;
        if(pre == "M_"){
            return this.myAtlas.getSpriteFrame(spriteFrameName);            
        }
        else if(pre == "B_"){
            return this.bottomAtlas.getSpriteFrame(spriteFrameName);
        }
        else if(pre == "L_"){
            return this.leftAtlas.getSpriteFrame(spriteFrameName);
        }
        else if(pre == "R_"){
            return this.rightAtlas.getSpriteFrame(spriteFrameName);
        }
    },
    getEmptySpriteFrame:function(side){
        if(side == "up"){
            return this.emptyAtlas.getSpriteFrame("e_mj_b_up");
        }   
        else if(side == "myself"){
            return this.emptyAtlas.getSpriteFrame("e_mj_b_up");
        }
        else if(side == "left"){
            return this.emptyAtlas.getSpriteFrame("e_mj_b_left");
        }
        else if(side == "right"){
            return this.emptyAtlas.getSpriteFrame("e_mj_b_right");
        }

    },
    getAudioURLByMJID:function(id){
        // var realId = 0;
        // if(id >= 0 && id < 9){
        //     realId = id + 21;
        // }
        // else if(id >= 9 && id < 18){
        //     realId = id - 8;
        // }
        // else if(id >= 18 && id < 27){
        //     realId = id - 7;
        // }
        // return "nv/" + realId + ".mp3";
    },
    sortMJ:function(mahjongs,dingque){
        var self = this;
        mahjongs.sort(function(a,b){
            if(dingque >= 0){
                var t1 = self.getMJType(a);
                var t2 = self.getMJType(b);
                if(t1 != t2){
                    if(dingque == t1){
                        return 1;
                    }
                    else if(dingque == t2){
                        return -1;
                    }
                }
            }
            return a - b;
        });
    },
    getSide:function(localIndex){
        return this._sides[localIndex];
    },
    
    getPre:function(localIndex){
        return this._pres[localIndex];
    },
    start () {

    },
    // update (dt) {},
});
