window.wsft = window.wsft || {}
var _socket = {
    url:"",
    webSocker:null,
    nodeName:"websocket",
    //链接
    connect:function(_url){
        this.url = _url;
        this.reconnect();
    },
    reconnect:function(){
        if(!this.url){
            return;
        }
        var that = this;
        this.killConnect();
        var ws = new WebSocket(this.url);
        cc.log(ws)
        this.webSocker = ws;
        ws.onopen = function (event) {
            cc.log("Send data WS was opened.");
            // var node = cc.director.getScene().getChildByName(that.nodeName);
            // if(node){
            //     node.destroy();
            // }
            //连接成功发送的第一条数据
        };
        ws.onmessage = function (event) {
            if(!event){
                return;
            }
            wsft.message.emit(wsft.protocol.MSG_HEART_BEAT,"");
            //心跳消息不处理
            // if(!msg || msg.length <= 0 || msg == '0000'){
            //     return;
            // }
            //分发收到的消息 RECEIVE_LOGIN = 2
            //协议号
            // wsft.message.emit("2","event"); //发送监听
            
        };
         // 连接出现问题时的回调
         ws.onerror = function (event) {
            cc.log("Send Text fired an error");
            wsft.uiFuncs.showTip("无法连接服务器，请检查网络。");
        };
        // 连接关闭之后的回调
        ws.onclose = function (event) {
            cc.log("WebSocket instance closed.");
            //断线重连

            
        };

    },

    //断开链接
    killConnect:function(){
        if(this.webSocker && this.webSocker.readyState == WebSocket.OPEN){
            this.webSocker.close();
            this.webSocker = null;
        } 
    },
    // 接收到消息之后的回调
    //发送消息
    sendMsg:function(msg){
        //连接不可用
        if (!this.isConnectState()){
            return;
        }
        

        this.webSocker.send(msg);
    },

    //连接状态
    isConnectState:function(){
        return this.webSocker && this.webSocker.readyState == WebSocket.OPEN;
    },
    
}
wsft._socket = module.exports = _socket;