
cc.Class({
    extends: cc.Component,

    properties: {
        my_hand_card_prefab:cc.Prefab,//自己手牌的预制体
        right_hand_card_prefab:cc.Prefab,//右手牌的预制体
        up_hand_card_prefab:cc.Prefab,//上手牌的预制体
        lsft_hand_card_prefab:cc.Prefab,//左手牌的预制体
        _myMJArr:[],//我的麻将数组
        _selectMJ:null,//选中的麻将
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {

        this.inintView()
        this.initcheckBox()
        this.initMusic()
        this.initAllBtn()
        this.onBeginGame()
    },

    start () {

    },
    //初始化界面
    inintView(){
        
        cc.find('Canvas/game_set').zIndex=10;
        var gameNode = cc.find('Canvas/game_node')
        this.mjCount = gameNode.getChildByName("mj_count").getComponent(cc.Label)
        this.mjCount.string = "剩余66张"
        this.juCount = gameNode.getChildByName("ju_count").getComponent(cc.Label)
        this.mjCount.string = "剩余6/6局"

        var myselfNode = gameNode.getChildByName("myselfNode")
        var myHandCard = cc.find('Canvas/game_node/myself_node/hand_card')//我的手牌node
        // myHandCard.active = false
        this._outCard = cc.find('Canvas/game_node/out_card_game')
        this._outCard.active = false
        //创建14张麻将
        for(var i = 0;i < 14;++i){
            var my_hand_card = cc.instantiate(this.my_hand_card_prefab);
            my_hand_card.x = -500 + i*75
            my_hand_card.y = 0
            if(i == 13){
                my_hand_card.x += 25
            }
            myHandCard.addChild(my_hand_card);
            var sprite = my_hand_card.getComponent(cc.Sprite)
            // sprite.spriteFrame = null
            this._myMJArr.push(sprite)
            my_hand_card.on('click', this.onOutCardClicked, this);
            this.initTouchHandCard(sprite.node)
        }

        //适配麻将
        var Winwidth = cc.winSize.width
        myHandCard.scaleX *= Winwidth/1280
        myHandCard.scaleY *= Winwidth/1280

        var sides = ['myself_node','right_node','up_node','left_node']
        for(var i = 0; i < sides.length;++i){
            var side = sides[i]
            var sideChilde = gameNode.getChildByName(side)

        }

    },
    //初始化玩法
    initGameInfo:function(){
    },
    //初始化玩法音乐
    initMusic:function(){
        //  wsft.audioFuncs.soundVolume;
        console.log(wsft.audioFuncs.musicVolume)
        var music_toggle = cc.find("Canvas/game_set/setBack_bg/music_toggle").getComponent(cc.Toggle)
        if (wsft.audioFuncs.musicVolume == 0){
            music_toggle.isChecked = false
        }else{
            music_toggle.isChecked = true
        }
    },
    onBeginGame:function(){
        var sides = ["right_node","up_node","left_node"];        
        var gameChild = this.node.getChildByName("game_node");
        for(var i = 0; i < sides.length; ++i){
            var sideChild = gameChild.getChildByName(sides[i]);
            var hand_card = sideChild.getChildByName("hand_card");
            for(var j = 0; j < 14; ++j){
                var other_hand_card = null
                if(sides[i] == "right_node"){
                    other_hand_card = cc.instantiate(this.right_hand_card_prefab);
                }else if(sides[i] == "up_node"){
                    other_hand_card = cc.instantiate(this.up_hand_card_prefab);
                }
                else if(sides[i] == "left_node"){
                    other_hand_card = cc.instantiate(this.lsft_hand_card_prefab);
                }
                if(sides[i] == "up_node"){
                    other_hand_card.x = 0 - 40*j
                    other_hand_card.y = 0
                    if(j == 13){
                        other_hand_card.x -= 20
                    }
                }else{
                    other_hand_card.x = 0
                    other_hand_card.y = 0 -25*j
                }
                hand_card.addChild(other_hand_card);
                var sprite = other_hand_card.getComponent(cc.Sprite)
                // sprite.spriteFrame = cc.vv.mahjongmgr.holdsEmpty[i+1];
            }            
        }
    },
    //自己手牌的触摸触摸事件
    initTouchHandCard(node){
        node.on(cc.Node.EventType.TOUCH_START,function(event){
            console.log("START")
            node.opacity = 255;
            this._outCard.active = false
            this._outCard.getComponent(cc.Sprite).spriteFrame = node.getComponent(cc.Sprite).spriteFrame
            this._outCard.x = event.getLocationX() - cc.winSize.width/2
            this._outCard.y = event.getLocationY() - cc.winSize.height/2
        }.bind(this));
        node.on(cc.Node.EventType.TOUCH_MOVE,function(event){
            console.log("MOVE")
            // console.log(event.getLocationY())
            this._outCard.active = true
            node.opacity = 150
            this._outCard.opacity = 255
            this._outCard.x = event.getLocationX() - cc.winSize.width/2
            this._outCard.y = event.getLocationY() - cc.winSize.height/2
            node.y = 0
        }.bind(this))
        node.on(cc.Node.EventType.TOUCH_END,function(event){
            console.log("END")
            this._outCard.active = false
            node.opacity = 255
            if(event.getLocationY() > 180){
                console.log('出牌==')
            }

        }.bind(this))
        node.on(cc.Node.EventType.TOUCH_CANCEL,function(event){
            console.log("TOUCH_CANCEL")
            this._outCard.active = false
            node.opacity = 255
            
            if(event.getLocationY() > 180){
                console.log('出牌==')
                // shootCard
            }else
            {
                if(this._selectMJ != null && this._selectMJ == node){
                    this._selectMJ.y = 15
                }
            }
            
        }.bind(this))
    },
    //自己手牌btn
    onOutCardClicked(event){
        // if(){//判断该出牌的是不是自己
        // }
        for(var i = 0 ; i< this._myMJArr.length ; ++i){
            if(event.target==this._myMJArr[i].node){
                if(event.target == this._selectMJ){//第二次点击出牌
                    console.log("出牌")
                    this.shootCard()
                    this._selectMJ.y = 0
                    this._selectMJ = null
                    return
                }
                if(this._selectMJ != null){
                    this._selectMJ.y = 0
                }
                event.target.y  = 15
                this._selectMJ = event.target
                return 
            }
        }
    },

    //出牌
    shootCard(_mjID){
        if(_mjID == null){
            return
        }
        //发送服务器
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "gps"){
           wsft.uiFuncs.showTip("敬请期待")
        }else if(name === "chat"){//聊天

           
        }else if(name === "set"){//设置

           
        }else if(name === "rule_btn"){//玩法

           
        }else{
            wsft.uiFuncs.showTip("功能为开发")
        }
    },
     // 初始化所有的btn
    initAllBtn:function(){
        var self = this
        var btnHader = function(btnPath,name){
           var btn = cc.find(btnPath)
           wsft.event.addBtnEvent(btn,self.node,'gameScene','allBtnCallback',name);
        }
        btnHader('Canvas/game_set/gps_btn','gps'); //微信
        btnHader('Canvas/game_set/setBack_bg/chat_btn','chat'); //chat
        btnHader('Canvas/game_set/setBack_bg/gps_btn','gps'); //gps
        btnHader('Canvas/game_set/setBack_bg/set_btn','set'); //set
        btnHader('Canvas/game_info/rule_btn',"rule_btn")//

    },
    //
    checkBoxCallback:function(event,name){
        console.log(event)
        if(name == "set_toggle"){
            var setBack_bg = cc.find('Canvas/game_set/setBack_bg')
            var gps_btn = cc.find('Canvas/game_set/gps_btn')
            if(event.node.getComponent(cc.Toggle).isChecked){
                setBack_bg.active = false
                gps_btn.active = true
            }else{
                setBack_bg.active = true
                gps_btn.active = false
            }
        }
        if(name == "music_toggle"){
            if(event.node.getComponent(cc.Toggle).isChecked){
                wsft.audioFuncs.setMusicVolume(wsft.audioFuncs.stopMusicVolume)
            }else{
                wsft.audioFuncs.stopMusicVolume = wsft.audioFuncs.musicVolume
                cc.sys.localStorage.setItem("stopMusicVolume",wsft.audioFuncs.stopMusicVolume);
                wsft.audioFuncs.setMusicVolume(0)
            }
        }
    },
    initcheckBox:function(){
        console.log("434343")
        var self = this 
        var checkBoxhander = function(_sliderPath,_name){
            var slider = cc.find(_sliderPath)
            wsft.event.addCheckBoxEvent(slider,self.node,'gameScene','checkBoxCallback',_name)
        }
        checkBoxhander('Canvas/game_set/set_toggle',"set_toggle")
        checkBoxhander('Canvas/game_set/setBack_bg/music_toggle',"music_toggle")
        
        
    },
    // update (dt) {},
});
