var pbCore = require("pbCore")
cc.Class({
    ctor:function(){
        this.sendMsg = [],//发送
        this.receiveMsg =[],//接受
        pbCore.preload(() => {
            this.pb();
        });
    },
    pb:function(){
        var loginpb = pbCore.loadFromFile('loginMessage.proto', 'msg.LoginMessage');
        this.sendMsg[wsft.protocol.SENT_LOGIN] = loginpb.LoginCm;
        this.receiveMsg[wsft.protocol.RECEIVE_LOGIN] = loginpb.LoginSm;

        // var innerpb = pbCore.loadFromFile('innerMessage.proto', 'msg.InnerMessage');
        // this.sendMsg[2] = innerpb.GsLoginBusHttp;

        // var mjpb = pbCore.loadFromFile('mjMessage.proto', 'msg.MjMessage');
        // this.sendMsg[3] = mjpb.Mj;

        // var roompb = pbCore.loadFromFile('roomMessage.proto', 'msg.RoomMessage');
        // this.sendMsg[4] = roompb.JulebuRoom;


    },
    //发送
    encodePB:function(msgId,msgTab){
        var self = this;
        var pbData = self.sendMsg[msgId];
        if(pbData == null){
            cc.error("encodePB=="+msgId+"PB is Null");
        }
        return pbData.encode(msgTab).toArrayBuffer();
    },
    //接受
    decodePB:function(msgId,msgTab){
        var self = this;
        var pbData = self.receiveMsg[msgId];
        if(pbData == null){
            cc.error("encodePB=="+msgId+"PB is Null");
        }
        return pbData.decode(msgTab);
    },

});
